import Vue from 'vue'
import Router from 'vue-router'
import Register from '@/components/Register'
import success from "../components/success";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Register',
      component: Register
    },
    {
      path:'/register',
      name:'success',
      component: success
    }
  ]
})



